































import { Component, Prop, Vue } from "vue-property-decorator";
import { namespace } from "vuex-class";

const localStorage = namespace('localStorage')

@Component({
  name: 'TetrisMenu'
})
export default class Settings extends Vue {
  @Prop()
  hasGameProgress!: boolean

  @localStorage.State
  indexScore

  @localStorage.Getter
  getScore

  isShow: boolean = false

  buttons: object[] = [
    {
      name: 'Resume',
      class: 'btn-green',
      isShow: this.hasGameProgress,
      click: this.onResume
    },
    {
      name: 'New game',
      class: this.hasGameProgress ? 'btn-orange' : 'btn-green',
      isShow: true,
      click: this.onNewGame
    },
    {
      name: 'Highscores',
      class: 'btn-blue',
      isShow: true,
      click: this.onHighscores
    },
  ]

  onResume() {
    this.$emit('tetris:resume')
  }

  onNewGame() {
    this.$emit('tetris:new-game')
  }

  onHighscores() {
    this.isShow = !this.isShow
  }

  isNewHighscore() {
    return !!this.getScore[this.indexScore]
  }
}
