export default {
  namespaced: true,
  state: {
    listScore: [],
    indexScore: -1
  },
  getters: {
    getScore: (state) => state.listScore.length ? state.listScore : JSON.parse(localStorage.getItem('score')) || [],
    getProgress: () => JSON.parse(localStorage.getItem('progress')) || {}
  },
  actions: {
    setScore({commit, state, getters}, {score, lines}) {
      const item = { score, lines }
      state.listScore = getters.getScore

      state.listScore.push(item)
      state.listScore.sort((a, b) => a.score < b.score).splice(5)
      state.indexScore = state.listScore.indexOf(item)

      localStorage.setItem('score', JSON.stringify(state.listScore))
    },
    setProgress({commit, state, getters}, progress) {
      localStorage.setItem('progress', JSON.stringify(progress))
    }
  }
}